import React, {memo, useEffect , useState} from 'react';
import Header from "../../../../partials/Header";
import SideBar from "../../../Partials/SideBar";
import {Link, NavLink} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import 'react-perfect-scrollbar/dist/css/styles.css';
import {Get_all_Package , DeleteOnePackage} from "../../../../../../entity/Redux/Doctors/Action";
import Loading from "../../../../partials/Loading";
import $ from 'jquery';
import M from "materialize-css";

export default memo(() => {

    const dispatch = useDispatch();
    const [packageData , setPackageData] = useState({});

    const PackageDataAll = useSelector(state => state.Doctor.PackageAll);

    useEffect(() => {
        dispatch(Get_all_Package());
        $(".load_more .close_icons").click(function () {
            var load_more = $(this).closest("div.box-package").find(".load_more");
            var price_pol = $(this).closest("div.box-package").find("#price_pol");
            load_more.removeClass("active_load_more");
            setTimeout(function () {
                price_pol.removeClass("active_pol");
            }, 300)
        })


        // load collapse
        var elems = document.querySelectorAll('.collapsible');
        M.Collapsible.init(elems);

    } , [])


    const DeletePackage = (e , id) => {
        e.preventDefault()
        dispatch(DeleteOnePackage(id));
    }


    function renderSwitch() {
        return PackageDataAll.data ? PackageDataAll.data.data.map((index) => (
                <div className="col s6 l3 m4" style={{
                    zIndex: 9,
                    position: "relative",
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    padding: "20px"
                }}>
                    <div className="box-package" serial-pack={index.name}>
                        <div id="pack_back"></div>
                        <div id="price_pol">
                            <span><span style={{float : 'left' , width : '40px' , fontSize : 13 , color : '#fff'}}>price : </span>{index.price} </span>
                            <span id="package_serial">{index.title}</span>
                        </div>

                        <div className={"content-pack"}>
                            {index.items.map(itemS => (
                                <>
                                    <ul style={{padding : '0px 10px' , margin : '0px'}}>
                                        <li style={{fontSize : '13px' , color : '#fff'}}>{itemS.name}</li>
                                        <li>
                                            <span style={{fontSize : '11px' , color : '#fff'}}>Unit : </span>
                                            <span style={{color : 'yellow' , fontSize : '13px'}}>{itemS.unit}</span>
                                        </li>
                                        {itemS.value ? (
                                            <li>
                                                <span style={{fontSize : '11px' , color : '#fff'}}>Value : </span>
                                                <span style={{color : 'yellow' , fontSize : '13px'}}>{itemS.value}</span>
                                            </li>
                                        ) : (
                                            <li>
                                                <span style={{fontSize : '11px' , color : '#fff'}}>Free : </span>
                                                <span style={{color : 'yellow' , fontSize : '13px'}}>{itemS.free ? "OK" : "NO"}</span>
                                            </li>
                                        )}
                                        <li></li>
                                    </ul>
                                    <div className={"divider"} style={{marginTop : '7px' , marginBottom : '7px'}}></div>
                                 </>
                            ))}
                        </div>


                        <div id="read_more">
                            <ul>
                                {index.active ? (
                                    <li id="delete_package" onClick={e => DeletePackage(e , index.id)}>
                                        <a style={{textTransform : 'none'}} className={"btn waves-effect waves-light btn-small green darken-4"}>Active</a>
                                    </li>
                                ) : (
                                    <li id="delete_package" onClick={e => DeletePackage(e , index.id)}>
                                        <a style={{textTransform : 'none'}} className={"btn waves-effect waves-light btn-small red darken-4"}>DeActive</a>
                                    </li>
                                )}

                                <Link to={"/adminp-e/"+index.id}><li><i className="material-icons">edit</i></li></Link>
                            </ul>
                        </div>
                        <div className="load_more" serial-key={index.serial}>
                                                    <span className="close_icons">
                                                        <i className="material-icons">close</i>
                                                    </span>
                            <p className="center-align">pack detail</p>

                            {/*{renderCollapse(index.detail)}*/}

                        </div>
                    </div>
                </div>
            )) : (
                <Loading />
        )
    }

    return (
        <div>

            <div id="loader-wrapper">
                <div id="loader"></div>
                <div className="loader-section section-left"></div>
                <div className="loader-section section-right"></div>
            </div>

            <Header/>
            <div id="main">
                <div className="wrapper">


                    <SideBar/>

                    <div id="back_shado"></div>
                    <div className="container">
                        <div className="row">
                            <div className="col s12">

                                <div className="col s12">
                                    <div id="breadcrumbs-wrapper">
                                        <div className="container">
                                            <div className="row">
                                                <div className="col s10 m6 l6">
                                                    <h5 className="breadcrumbs-title">Package Manager</h5>
                                                    <ol className="breadcrumbs">
                                                        <li><NavLink to="/doctor">
                                                            <a>Dashboard</a>
                                                        </NavLink></li>
                                                        <li><a className="active">Package Manager</a></li>
                                                    </ol>
                                                </div>

                                            </div>
                                        </div>

                                        <NavLink to="/doctor-add-package">
                                            <a id="adding_float_btn"
                                                                      className="btn btn-floating green darken-2 waves-effect">
                                            <i className="material-icons">add</i>
                                        </a>
                                        </NavLink>
                                    </div>
                                </div>


                                <div className="col s12">
                                    {renderSwitch()}
                                </div>


                            </div>
                        </div>
                    </div>
                </div>
            </div>


            <div id="back_detail">
                <div className="container">
                    <div className="row">
                        <div className="col s12">
                            <div className="col s1 m2 l4"></div>
                            <div className="col s10 m8 l4" style={{marginTop: 30}}>
                                <div className="card grey darken-4">
                                    <div className="card-content white-text">
                                        <span className="card-title">Delete Package</span>
                                        <p>Do you want to delete the <span id="serial_pp"></span> package?</p>
                                    </div>
                                    <div className="card-action">
                                        <a className="btn btn-secondary waves-effect red" id="deleted" style={{
                                            textTransform: 'lowercase',
                                            marginBottom: 15,
                                            float: "left"
                                        }}>delete</a>
                                        <a className="btn btn-secondary waves-effect blue darken-3" id="cancel"
                                           style={{
                                               textTransform: 'lowercase',
                                               marginBottom: 15,
                                               float: 'right'
                                           }}>back</a>

                                    </div>
                                </div>
                            </div>
                            <div className="col s1 m2 l4"></div>
                        </div>
                    </div>
                </div>

            </div>


        </div>
    );
})


